:root {
  --active-highlight-color: #6e9ef8;
  --body-bg-color: #9e9e9e;
  --kl-color: #2a64f8;
  --ui-bg-color: #ddd;
}

@keyframes consoleIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes consoleInFast {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }
}

@keyframes consoleOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  overscroll-behavior-x: none;
  --lightningcss-light: initial;
  --lightningcss-dark: ;
  color-scheme: light;
  font-size: 16px;
  overflow: hidden;
}

body, html {
  -webkit-tap-highlight-color: #0000;
  touch-action: none;
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  text-size-adjust: none;
}

.g-root {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 16px;
}

a:link, a:not([href]) {
  color: var(--kl-color);
  cursor: pointer;
  text-decoration: underline;
}

a:visited {
  color: #536184;
  text-decoration: underline;
}

a:hover, a:not([href]):hover {
  color: #000;
  text-decoration: underline;
}

.kl-select {
  background: #fff;
}

.grid-hr {
  clear: both;
  border-top: 1px solid #00000026;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
}

.kl-toolspace {
  background-color: var(--ui-bg-color);
}

.kl-toolspace--left {
  border-right: 1px solid #878787;
}

.kl-toolspace--right {
  border-left: 1px solid #878787;
}

.kl-toolspace-row {
  background: linear-gradient(to top, #fff0 20%, #fff9 100%);
  box-shadow: 0 1px 3px #0000001a, 0 1px #cecece;
}

.nohighlight {
  -webkit-tap-highlight-color: #fff0;
}

.toolspace-row-button {
  cursor: pointer;
  flex-grow: 1;
}

.toolspace-triangle-button {
  cursor: pointer;
  -webkit-tap-highlight-color: #fff0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.toolspace-svg-triangle-button {
  cursor: pointer;
  -webkit-tap-highlight-color: #fff0;
}

.toolspace-row-button-hover {
  background: #ffffffb3;
}

.toolspace-row-button:active, .toolspace-triangle-button:active {
  background-color: #ddd;
  box-shadow: inset 2px 2px 10px #0003 !important;
}

.toolspace-svg-triangle-button-hover {
  fill: #ffffffb3;
}

.toolspace-svg-triangle-button:active {
  fill: #ddd;
  filter: url("#innershadow");
}

.toolspace-row-button-activated {
  box-shadow: inset 0 2px 0 0 var(--active-highlight-color);
  pointer-events: none;
  background: #fff;
}

.toolspace-row-button-activated div {
  opacity: 1 !important;
}

.toolspace-row-button-disabled {
  opacity: .5;
  pointer-events: none;
}

.kl-tool-row-border-right {
  border-right: 1px solid #d4d4d4;
}

.kl-color-picker__h, .kl-color-picker__sv, .kl-color-picker__preview {
  box-shadow: 0 0 0 1px #bcbcbc;
}

.kl-color-picker--active {
  z-index: 1;
  box-shadow: 0 0 0 1px #fff;
}

.kl-color-picker__secondary {
  box-shadow: 0 0 0 1px #bcbcbc;
}

.color-picker-preview-button {
  cursor: pointer;
  color: #000;
  opacity: .8;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.color-picker-preview-button-hover {
  opacity: 1;
  background-color: #fff;
  filter: none !important;
}

.color-picker-preview-button-active {
  opacity: 1;
  box-shadow: inset 0 2px 0 0 var(--active-highlight-color);
  background-color: #fff;
  filter: none !important;
}

.slider-wrapper {
  text-shadow: 0 0 5px #ffffff80;
  color: #333;
  cursor: ew-resize;
  background: linear-gradient(#909090 1%, #b3b3b3 73%);
  border-radius: 8px;
  box-shadow: .5px 1px #fffc;
}

.slider-wrapper--active {
  color: #000;
  background: linear-gradient(#7a7a7a 1%, #939393 73%);
  box-shadow: 0 0 1px 1px #0003;
}

.slider-wrapper--active .slider-inner {
  background: #fff;
  box-shadow: 2px 2px 10px #919191;
}

.slider-inner {
  background-color: #fff;
  background-image: linear-gradient(to top, #e8e8e8 0%, #fff 70%);
  border-radius: 6px 0 0;
  box-shadow: inset 1px 1px 1px #0000004d, 2px 2px 6px #9191917d;
}

.kl-point-slider__line {
  background: #aaa;
  height: 2px;
}

.kl-point-slider__point {
  cursor: ew-resize;
  background-color: #eaeaea;
  transition: box-shadow .2s ease-in-out;
  position: absolute;
  top: 0;
  box-shadow: 0 0 3px #000c;
}

.kl-point-slider__point:active {
  box-shadow: 0 0 6px #000;
}

button {
  color: #666;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  background-color: #f3f3f3;
  background-image: linear-gradient(to top, #6f6f6f1a 0%, #fff0 60%, #ffffff4d 100%);
  border: none;
  border-radius: 4px;
  min-width: 30px;
  padding: 5px;
  font-size: 15px;
  line-height: 17px;
  box-shadow: 0 0 0 1px #0000000a, 0 1px #0000001a, 0 2px 3px #0000000d;
}

button:hover {
  color: #777;
  cursor: pointer;
  background-color: #fff;
}

button:active {
  background-color: #ddd;
  box-shadow: inset 2px 2px 10px #0003, 0 2px 2px #ddd;
}

button:disabled {
  color: #999;
  cursor: default;
  background-color: #ddd;
  background-image: linear-gradient(to top, #fff0 60%, #ffffff4d 100%);
  border: none;
  border-radius: 4px;
  min-width: 30px;
  padding: 5px;
  font-size: 15px;
  box-shadow: 0 0 0 1px #0000000d;
}

button:disabled img {
  opacity: .2;
}

button img {
  float: left;
  pointer-events: none;
}

.kl-button-primary {
  background: var(--kl-color);
  color: #fff;
}

.kl-button-primary:hover {
  background-image: linear-gradient(to top, var(--kl-color) -50%, var(--active-highlight-color) 150%);
  color: #fff;
}

.kl-button-primary:active {
  background-image: linear-gradient(to top, var(--kl-color) -50%, var(--active-highlight-color) 120%);
  color: #fff;
  box-shadow: unset;
}

.grid-button {
  width: 120px;
  margin-top: 10px;
  margin-left: 10px;
}

.grid-button--filter {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.kl-popup {
  touch-action: pan-y;
  background: #32323280;
  width: 100%;
  height: 100%;
  animation-name: consoleIn;
  animation-duration: .3s;
  animation-timing-function: ease-out;
  overflow: hidden auto;
}

.kl-popup-box {
  background-color: #ddd;
  border-radius: 10px;
  margin: 10px 0;
  padding: 20px;
  position: relative;
  box-shadow: 0 5px 60px #00000073;
}

.kl-popup-box--sm {
  width: 340px;
}

.kl-popup__btn {
  justify-content: center;
  align-items: center;
  min-width: 80px;
  margin-top: 8px;
  margin-left: 8px;
  display: flex;
}

.kl-popup__btn img {
  margin-left: 2px;
  margin-right: 5px;
}

.kl-popup__btn__text {
  text-align: center;
  flex-grow: 1;
}

.kl-popup__icon-error, .kl-popup__icon-ok, .kl-popup__icon-warning, .kl-popup__icon-upload {
  float: left;
  background-repeat: no-repeat;
  background-size: cover;
  width: 1.1rem;
  height: 1.1rem;
  margin-right: 5px;
}

.kl-popup__icon-error {
  background-image: url("error.abae1a5e.svg");
}

.kl-popup__icon-ok {
  background-image: url("ok.398afd66.svg");
}

.kl-popup__icon-warning {
  background-image: url("warning.7472a070.svg");
}

.kl-popup__icon-upload {
  background-image: url("upload-large.bb0f452b.svg");
}

.kl-d-modal-root {
  background: #00000073;
}

.kl-d-modal {
  background: #eee;
  box-shadow: 0 5px 60px #00000040;
}

@keyframes topOverlayPulse {
  10% {
    background-color: var(--kl-color);
    padding-top: 12px;
  }

  50% {
    background-color: var(--kl-color);
    padding-top: 7px;
  }
}

.top-overlay {
  z-index: 100;
